/* ===============================
   Global Stylesheet
   =============================== */

   *::before,
   *::after {
     margin: 0;
     padding: 0;
     box-sizing: border-box;
   }
     html, body {

      height: 100%;
      width: 100%;
      
     }

.main {

   height : 100%;
   width: 100%;
}

/* === Header Styles === */
.header {
  background-color: #E5CA5A;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
  text-align: center;
  padding: 0;
}

h1 {
  font-family: "Gilroy", serif;
  font-weight: 400;
  font-style: normal;
}

.Gilroy-bold {
  font-family: "Gilroy", serif;
  font-weight: 700;
  font-style: normal;
}

.header-content {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.profile-pic-container {
  width: 250px;
  height: 250px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  border-radius: 50%;
  overflow: hidden;
  background-color: rgba(0, 0, 0, 0.1);
  margin-bottom: 20px;
  transition: transform 0.3s ease;
}

.profile-pic-container:hover {
  transform: scale(1.1);
}

.profile-pic {
  width: 250px;
  height: 250px;
  border-radius: 50%;
  margin-bottom: 20px;
  object-fit: cover;
  transition: transform 0.3s ease;
  margin-top: 20px;
}

.profile-pic:hover {
  transform: scale(1.1);
}

.ripple {
  position: absolute;
  width: 150%;
  height: 150%;
  top: 50%;
  left: 50%;
  background: rgba(255, 255, 255, 0.5);
  border-radius: 50%;
  transform: translate(-50%, -50%) scale(0);
  animation: rippleEffect 0.6s ease-out;
  z-index: 1;
}

@keyframes rippleEffect {
  to {
    transform: translate(-50%, -50%) scale(1);
    opacity: 0;
  }
}

@media (max-width: 768px) {
  .profile-pic-container {
    width: 120px;
    height: 120px;
  }

  .profile-pic {
    width: 120px;
    height: 120px;
  }
}

/* === Navbar Styles === */
/* Navbar Styling */
.navbar {
  background-color: rgba(34, 34, 34, 0.9);
  padding: 10px 20px;
  position: sticky;
  top: 0;
  z-index: 1000;
  border-radius: 0 0 20px 20px;
  width: 40%;
  margin: 0 auto;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.5);
  transition: background-color 0.3s ease, box-shadow 0.3s ease, transform 0.3s ease; 
}

/* When navbar is hidden */
.navbar.hidden {
  transform: translateY(-100%); 
}

/* Body Styling */
body {
  background-color: #E5CA5A;
  margin: 0;
  padding: 0;
}

/* Navbar List Styling */
.nav-list {
  list-style: none;
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin: 0;
  padding: 0;
}

/* Navbar Item Styling */
.nav-item {
  margin: 0 10px;
  color: #fff;
  font-size: 16px;
  font-weight: 500;
  cursor: pointer;
  position: relative;
  transition: color 0.3s ease;
}

.nav-item:hover {
  color: #ffd700;
}

.nav-item:after {
  content: "";
  position: absolute;
  bottom: -5px;
  left: 50%;
  width: 0;
  height: 2px;
  background: #ffd700;
  transition: all 0.3s ease;
  transform: translateX(-50%);
}

.nav-item:hover:after {
  width: 100%;
}

/* Media Queries for Mobile */
@media (max-width: 768px) {
  .navbar {
    width: 90%;
    padding: 10px 20px;
  }

  .nav-list {
    flex-direction: column;
    align-items: center;
  }

  .nav-item {
    margin: 10px 0;
    font-size: 14px;
  }
}


