/* ----------------ABOUT.ME. CSSS ----------------- */
.about-me {
  padding: 50px 20px;
  background-color: #070A0F;
  background-image: url('https://images.unsplash.com/photo-1468436139062-f60a71c5c892?q=80&w=2070&auto=format&fit=crop&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D');
  background-size: 30%;
  background-position: center 30%;
  background-position: bottom;
  background-repeat: no-repeat;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height:400px;
  
}

.about-me h2 {
  
  text-align: center;
  font-size: 2rem;
  margin-bottom: 30px;
  color: white;
