/* Social Media Bar */
.social-media-bar {
  display: flex;
  justify-content: center;
  gap: 20px;
  padding: 20px 0;
  background-color: #CFCDD0;
  border-radius: 50px;
  width: 30%; 
  height: 10px;
  margin: 0 auto;
  align-items: center;
  position: relative;
  bottom: -100px; 
}

.social-media-bar a {
  text-decoration: none;
  font-size: 1.5rem;
  color: black;
  background-color: #E5CA5A;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  transition: transform 0.3s ease, background-color 0.3s ease;
}

.social-media-bar a:hover {
  transform: scale(1.2);
  background-color: #555;
}

.social-media-bar a i {
  font-size: 1.2rem;
  color: white;
}

/* Responsive Design for Social Media Bar */
@media (max-width: 768px) {
  .social-media-bar {
    width: 70%; 
    bottom: -60px; 
    flex-wrap: wrap;
    gap: 15px;
  }

  .social-media-bar a {
    width: 40px;
    height: 40px;
    font-size: 1rem;
  }
}

@media (max-width: 480px) {
  /* Further adjustments for very small screens */
  .social-media-bar {
    width: 100%; 
    bottom: -50px; 
    justify-content: center;
  }

  .social-media-bar a {
    width: 40px;
    height: 40px;
    font-size: 1rem; 
  }
}

