/* Timeline */
.timeline {
  position: relative;
  margin: 0 auto;
  padding: 20px 0;
  max-width: 800px;
}

.timeline:before {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 50%;
  width: 3px;
  background: #3498DB;
  transform: translateX(-50%);
}

.timeline-item {
  display: flex;
  align-items: flex-start;
  margin: 40px 0;
  position: relative;
}
/* Existing Styles */
.timeline-item:nth-child(odd) .timeline-content {
  position: relative;
  transform: translateX(80%);
}

.timeline-item:nth-child(even) .timeline-content {
  position: relative;
  transform: translateX(-30%);
}

.timeline-icon {
  position: absolute;
  top: 0;
  left: 50%;
  width: 40px;
  height: 40px;
  background-color: #3498DB;
  color: white;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1;
}

.timeline-content {
  padding: 25px 30px;
  background: white;
  border-radius: 10px;
  box-shadow: 0 6px 10px rgba(0, 0, 0, 0.1);
  width: 60%;
}

/* Icons Section */
.icons {
  margin-top: 15px;
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
  justify-content: center;
}

.icon-circle {
  width: 50px;
  height: 50px;
  background-color: #f4f4f4;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.tech-icon {
  width: 24px;
  height: 24px;
}

/* Media Query for Tablets and Small Laptops (max-width: 768px) */
@media (max-width: 768px) {
  /* Timeline Items */
  .timeline-item:nth-child(odd) .timeline-content,
  .timeline-item:nth-child(even) .timeline-content {
    width: 80%;
    transform: translateX(0); 
    margin: 0 auto;
  }

  /* Timeline Line */
  .timeline:before {
    left: 50%;
    transform: translateX(-50%); 
  }

  /* Timeline Icon */
  .timeline-icon {
    width: 35px;
    height: 35px;
  }

  /* Content Width Adjustment */
  .timeline-content {
    width: 85%;
  }

  /* Icon size for smaller screens */
  .tech-icon {
    width: 20px;
    height: 20px;
  }
}

/* Media Query for Small Mobile Devices (max-width: 480px) */
@media (max-width: 480px) {
  /* Timeline Items */
  .timeline-item:nth-child(odd) .timeline-content,
  .timeline-item:nth-child(even) .timeline-content {
    width: 90%;
    transform: translateX(0); 
    margin: 0 auto;
  }

  /* Timeline Line */
  .timeline:before {
    left: 50%;
    transform: translateX(-50%); 
  }

  /* Timeline Icon */
  .timeline-icon {
    width: 30px;
    height: 30px;
  }

  /* Content Width Adjustment */
  .timeline-content {
    width: 95%;
  }

  /* Icon size for mobile */
  .tech-icon {
    width: 18px;
    height: 18px;
  }
}

  /* ---------------------------------------------
  ----------------PROJECT.CSS------------------------------------- */

/* Main container for the projects section */
.projects-section {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color:rgb(32, 61, 126); 
  padding: 50px 0;
}

/* Father box styling */
.projects-father-box {
  background: white;
  width: 75%;
  max-width: 1200px;
  border-radius: 20px;
  padding: 40px;
  box-shadow: inset 0 10px 30px rgba(229, 202, 90, 0.4); 
  text-align: center;
}

/* Title and subtitle */
.projects-title {
  font-size: 2.5rem;
  font-weight: bold;
  color: #333;
  margin-bottom: 10px;
  position: relative;
  top: -45px;
}
.projects-subtitle {
  font-size: 1.2rem;
  color: #666;
  margin-bottom: 30px;
  position: relative;
  top: -53px;
}

/* Projects grid */
.projects-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 20px; 
  width: 100%;
  justify-items: center;
  position: relative;
  top: -60px;
}

/* Individual project cards */
.project-card {
  width: 60%;
  height: 130px; 
  background-color: #fff;
  border-radius: 10px;
  overflow: hidden;
  cursor: pointer;
  transition: transform 0.3s, box-shadow 0.3s;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

/* Add image styling inside project cards */
.project-image {
  width: 100%;
  height: 100%;
  object-fit: cover; 
}

/* Hover effect */
.project-card:hover {
  transform: scale(1.05);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.3);
}

/* Responsive Design */
@media (max-width: 768px) {
  .projects-father-box {
      width: 90%; 
  }
  .projects-title {
      font-size: 2rem;
  }
  .projects-grid {
      grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  }
}



  --------------------------------------------------------
  ------------------SKILLS CSSSS-------------------------------------
  
/* Skills Wrapper - Centered and Responsive */
.skills-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 40px;
  background-color: #f5f5f5;
  border-radius: 10px;
  margin: 20px auto;
  max-width: 80%;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  height: 80vh;
}

/* Title */
.skills-title {
  font-size: 2rem;
  font-weight: 700;
  color: #2c3e50;
  margin-bottom: 20px;
  text-align: center; /* Ensures title is centered */
}

/* Skills Container */
.skills-container {
  display: flex;
  flex-wrap: wrap;
  background: #CFCDD0;
  border-radius: 50px;
  gap: 20px;
  justify-content: center;
  align-items: center;
  margin: 50px auto;
  padding: 20px;
  width: 70%;
  min-height: 40px;
}

/* Skill Item */
.skill-item {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: #f0c420;
  font-size: 2rem;
  cursor: pointer;
  transition: transform 0.3s, background-color 0.3s, opacity 0.3s;
  transform: translate(2px, 2px);
}

/* Icon inside Skill Item */
.skill-item i {
  font-size: 2rem;
  color: #fff;
  transition: opacity 0.3s ease-in-out;
}

/* Tooltip-style percentage */
.skill-item .skill-percentage {
  position: absolute;
  font-size: 16px;
  color: #fff;
  display: none;
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
}

/* Hover Effect */
.skill-item:hover {
  transform: scale(1.1);
  background-color: #f0a420;
}

.skill-item:hover i {
  opacity: 0;
}

.skill-item:hover .skill-percentage {
  display: block;
  opacity: 1;
}

