  -------------------------CONTACT.CSSS-----------------------------------------------

  .contact-section {
    padding: 60px 20px;
    background-color: #1e1e1e;
    color: white;
    text-align: center;
    min-height: 100vh;
    width: 100%;
    box-sizing: border-box;
}

.contact-title {
    font-size: 32px;
    margin-bottom: 20px;
    font-weight: bold;
    text-align: center; 
}

.contact-info {
    margin-bottom: 30px;
    display: flex;
    justify-content: center;
    gap: 40px;
    flex-wrap: wrap;
    box-sizing: border-box;
}

.contact-item {
    font-size: 18px;
    display: flex;
    align-items: center;
    gap: 10px;
}

.contact-item i {
    font-size: 20px;
    color: rgb(240, 237, 229);
}

.contact-form {
    max-width: 600px;
    margin: 0 auto;
    background-color: #333;
    padding: 20px;
    border-radius: 8px;
    box-sizing: border-box;
}

.contact-form .form-row {
    display: flex;
    gap: 10px;
    margin-bottom: 15px;
    flex-wrap: wrap;
}

.contact-form input,
.contact-form textarea {
    width: 100%; 
    max-width: 580px; 
    padding: 10px;
    border: 1px solid #555;
    border-radius: 5px;
    background-color: #222;
    color: white;
    font-size: 16px;
    outline: none;
    box-sizing: border-box; 
}

.contact-form textarea {
    resize: none;
    height: 150px;
    margin-top: 15px;
    margin-bottom: 15px;
}

.contact-form .submit-btn {
    width: 100%;
    padding: 10px 20px;
    background-color: #f7b500;
    border: none;
    border-radius: 5px;
    font-size: 16px;
    font-weight: bold;
    color: #1e1e1e;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.contact-form .submit-btn:hover {
    background-color: #d99400;
}

.made-with-love {
    text-align: center;
    margin-top: 20px;
    font-family: "Cursive", sans-serif;
    font-size: 20px;
    color: white;
}

.made-with-love .heart-icon {
    color: white;
    background-color: none;
    border-radius: 50%;
    padding: 5px;
    font-size: 18px;
    display: inline-block;
    vertical-align: middle;
    line-height: 1;
}

/* Media Queries for Responsive Design */
@media (max-width: 768px) {
    /* Stack the contact-info items vertically */
    .contact-info {
        flex-direction: column;
        gap: 20px;
        align-items: center;
    }

    /* Contact form rows stack vertically */
    .contact-form .form-row {
        flex-direction: column;
    }

    /* Ensure input and textarea fields fill the width */
    .contact-form input,
    .contact-form textarea {
        width: 100%;
        max-width: 100%;
    }

    /* Adjust title size for smaller screens */
    .contact-title {
        font-size: 28px;
    }

    /* Center the form on smaller screens */
    .contact-form {
        padding: 15px;
    }

    .made-with-love {
        font-size: 18px;
    }
}

@media (max-width: 480px) {
    /* Reduce font size for smaller screens */
    .contact-title {
        font-size: 24px;
    }

    .contact-form input,
    .contact-form textarea {
        padding: 8px;
    }

    .contact-form .submit-btn {
        font-size: 14px;
    }

    .made-with-love {
        font-size: 16px;
    }
}
